import WebApi from "api/WebApi";
export function UploadImage(fileData) {
    return WebApi.Post("upload/images", fileData);
}
export function UploadAvatar(fileData) {
    return WebApi.Post("upload/avatars", fileData);
}
export default {
    UploadImage,
    UploadAvatar
};



